.products-section {
  background: $bg-primary-muted;
  &__row {
    padding: 0 15px;
  }
  &__item {
    padding: 10px;
    @media (max-width: 991px) {
      flex: 0 0 50%;
    }
    @media (max-width: 767px) {
      flex: 1 0 100%;
    }
  }
  &__title {
    padding: 0 10px;
  }
}
