*::selection {
  color: $light;
  background: $primary;
  box-sizing: border-box;
  font-family: "Nunito Sans", sans-serif;
}
* {
  font-family: "Nunito Sans", sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scrollbar-width: none;
}
body {
  margin: 0;
}
a {
  transition: 0.3s all ease;
  text-decoration: none;
  &:active {
    color: inherit;
  }
}
li {
  list-style-type: none;
}
ul {
  padding: 0;
  margin: 0;
}
